html {
    font-size: 62.5%;
    overflow-x: hidden;
    scroll-behavior: smooth;
}

@mixin grid($val) {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax($val, 1fr));
    gap: 1.5rem;
}

.header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000;
    background: rgb(194, 231, 254);
    box-shadow: 0 .5rem 1rem rgba(0, 0, 0, .1);
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 2rem 9%;
    .logo {
        font-size: 2.5rem;
        font-weight: bolder;
        color: black;
    }
    i {
        color: #666;
        padding-right: .5rem;
    }
    .navbar {
        a {
            font-size: 1.7rem;
            color: #666;
            margin: 0 1rem;
            &:hover {
                color: white;
            }
        }
    }
    .icons div {
        color: #666;
        padding-right: 1.5rem;
        font-size: 1.7rem;
        &:hover {
            color: white;
        }
    }
    .login-form {
        position: absolute;
        top: 115%;
        right: -100%;
        background: white;
        border-radius: .5rem;
        box-shadow: 0 .5rem 1rem rgba(0, 0, 0, .1);
        width: 35rem;
        padding: 2rem;
        &.active {
            right: 1%;
            transition: .4s linear;
        }
        h3 {
            color: black;
            text-transform: uppercase;
            font-size: 2.2rem;
            text-align: center;
            margin-bottom: .7rem;
        }
        .box {
            margin: .7rem 0;
            border: .1rem solid rgba(0, 0, 0, .1);
            padding: 1rem 1.2rem;
            font-size: 1.5rem;
            color: black;
            text-transform: none;
            width: 100%;
        }
        .remember {
            display: flex;
            align-items: center;
            gap: 1rem;
            margin: 1rem 0;
            label {
                font-size: 1.5rem;
                color: #666;
                cursor: pointer;
            }
        }
        .btn {
            margin: 1rem 0;
            width: 100%;
            text-align: center;
        }
        p {
            color: #666;
            padding-top: .7rem;
            font-size: 1.4rem;
            a {
                color: rgb(194, 231, 254);
                &:hover {
                    text-decoration: underline;
                }
            }
        }
    }
#map {
    height: 650px;
}

.journey-form {
    // position: absolute;
    // top: 115%;
    // right: -100%;
    background: white;
    border-radius: .5rem;
    box-shadow: 0 .5rem 1rem rgba(0, 0, 0, .1);
    padding: 2rem;
    .box {
        margin: .7rem 0;
        border: .1rem solid rgba(0, 0, 0, .1);
        padding: 1rem 1.2rem;
        font-size: 1.5rem;
        color: black;
        text-transform: none;
        width: 100%;
    }
    .btn {
        margin: 1rem 0;
        width: 100%;
        height: 30px;
        text-align: center;
        font-size: 140%;
    }
    label {
        font-size: 140%;
    }
    select {
        font-size: 140%;
    }
}

.flex-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    .box1 {
        flex-grow: 3;
        flex: 1.5;
        background: white;
        border-radius: .5rem;
        box-shadow: 0 .5rem 1rem rgba(0, 0, 0, .1);
        padding: 2rem;
        #result {
            height: 200px;
        }
        h1 {
            font-size: 20px;
        }
    }
    .box2 {
        flex-grow: 4;
        flex: 2;
    }
}

@media (max-width: 800px) {
    .flex-container {
        flex-direction: column;
    }
}

#time {
    display: flex;
    align-items: center;
    gap: 3rem;
    margin: 2% 10%;
    .date {
        font-size: 120%;
        border: .2rem solid rgba(0, 0, 0, .1);
        min-width: 100px;
    }
    .time {
        font-size: 120%;
        border: .2rem solid rgba(0, 0, 0, .1);
        min-width: 80px;
    }
}
}

